export default {
  name: 'header-banner',
  components: {
  },
  data() {
    return {
      swiperOptions: {
        spaceBetween: 30,
        centeredSlides: true,
        pagination: '.swiper-pagination',
        loop: true,
        autoplay: {
          delay: 2500
          // disableOnInteraction: false
        }
        // 每张播放时长3秒，自动播放
        // Some Swiper option/callback..
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper
    }
  },
  mounted() {
    console.log('Current Swiper instance object', this.swiper)
    this.swiper.slideTo(3, 1000, false)
  }
}



